.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-loading-parent--relative {
  position: relative !important; }

.tu-loading-parent--hidden {
  overflow: hidden !important; }

.tu-loading-mask {
  position: absolute;
  z-index: 2000;
  background-color: rgba(255, 255, 255, 0.5);
  margin: 0;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: opacity 0.3s; }
  .tu-loading-mask.is-fullscreen {
    position: fixed; }
    .tu-loading-mask.is-fullscreen .tu-loading-spinner {
      margin-top: -25px; }
      .tu-loading-mask.is-fullscreen .tu-loading-spinner .circular {
        height: 50px;
        width: 50px; }

.tu-loading-spinner {
  top: 50%;
  margin-top: -21px;
  width: 100%;
  text-align: center;
  position: absolute; }
  .tu-loading-spinner .tu-loading-text {
    color: #6d5dfc;
    margin: 3px 0;
    font-size: 14px; }
  .tu-loading-spinner .circular {
    height: 42px;
    width: 42px;
    animation: loading-rotate 2s linear infinite; }
  .tu-loading-spinner .path {
    animation: loading-dash 1.5s ease-in-out infinite;
    stroke-dasharray: 90, 150;
    stroke-dashoffset: 0;
    stroke-width: 2;
    stroke: #6d5dfc;
    stroke-linecap: round; }
  .tu-loading-spinner i {
    color: #6d5dfc; }

.tu-loading-fade-enter,
.tu-loading-fade-leave-active {
  opacity: 0; }

@keyframes loading-rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes loading-dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -40px; }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -120px; } }
